import React from 'react'
import { Button, Card, Div, Gallery, Header, Link, Panel, PanelHeader, PanelHeaderBack, Placeholder, Spinner, Tabs, TabsItem } from '@vkontakte/vkui'
import { Icon56GhostOutline } from '@vkontakte/icons';
import { useDispatch, useSelector } from 'react-redux'
import LessonRow from '../../components/LessonRow'
import { RootState } from '../../store/rootReducer'
import { DayRange, PanelProps, WeekRange } from '../../types'
import { getLessonsInDay } from '../../utils'
import './Schedule.sass'
import { requestSchedule } from '../../store/slices/schedule';
import { setActivePanel } from '../../store/slices/navigation';
import { MAIN_PANEL } from '../../constants';

const DAYS = [
  { id: 0, title: 'ПН' },
  { id: 1, title: 'ВТ' },
  { id: 2, title: 'СР' },
  { id: 3, title: 'ЧТ' },
  { id: 4, title: 'ПТ' },
  { id: 5, title: 'СБ' },
]
const WEEKS = [
  { id: 0, title: 'Числитель'},
  { id: 1, title: 'Знаменатель'},
]

const SchedulePanel: React.FC<PanelProps> = ({
  id,
}) => {
  const dispatch = useDispatch()
  const { isLoading, error, schedule } = useSelector((s: RootState) => s.schedule)

  const [currentDay, setCurrentDay] = React.useState<DayRange>(0)
  const [currentWeek, setCurrentWeek] = React.useState<WeekRange>(0)

  const goBack = () => {
    dispatch(setActivePanel(MAIN_PANEL))
  }

  const cardRef = React.useRef<HTMLDivElement>(null)

  let content

  if(isLoading)
  {
    content = <Spinner />
  }
  else if(error)
  {
    content = <Placeholder
      stretched
      action={
        <Button onClick={() => {
          dispatch(requestSchedule(error.payload))
        }}>Повторить</Button>}
    >{error.message}</Placeholder>
  }
  else if(schedule)
  {
    content = ( <>      
      <div className="schedule__card" ref={cardRef}>
        <Gallery
          style={{height:'100%'}}
          slideIndex={currentDay}
          onChange={d => setCurrentDay(d as DayRange)}
        >
          {DAYS.map((_,i) => {
            const lessons = getLessonsInDay(schedule.lessons, i as DayRange, currentWeek)

            return <Card key={i} className="schedule__body">
              {lessons.length === 0
                ? <Placeholder className="schedule__empty-placeholder" icon={<Icon56GhostOutline style={{color:"teal"}}/>}>Выходной</Placeholder>
                : lessons.map((lesson, j) => {
                    return <LessonRow key={[i,j].join('-')} lesson={lesson} />
                  })}
            </Card>
          })}
        </Gallery>
      </div>

      <Card style={{marginTop:8}}>
        <Header>
          <Link>{schedule.title}</Link>
        </Header>
        <Tabs>
          {WEEKS.map(week => {
            return <TabsItem
              key={week.id}
              selected={week.id === currentWeek}
              onClick={() => setCurrentWeek(week.id as WeekRange)}
            >{week.title}</TabsItem>
          })}
        </Tabs>
        <Tabs>
          {DAYS.map(day => {
            return <TabsItem
              key={day.id}
              selected={day.id === currentDay}
              onClick={() => setCurrentDay(day.id as DayRange)}
            >{day.title}</TabsItem>
          })}
        </Tabs>
      </Card>
    </>)
  }

  return (
    <Panel id={id}>
      <PanelHeader
        separator={false}
        left={
          <PanelHeaderBack onClick={goBack} />
        }
      >Расписание</PanelHeader>
      <Div className="schedule__wrapper">
        {content}
      </Div>
    </Panel>
  )
}

export default SchedulePanel