import React from 'react'
import { Banner, Card, Cell, Div, InfoRow, Panel, Progress } from '@vkontakte/vkui'
import { Icon28Search, Icon28SettingsOutline } from '@vkontakte/icons';
import { PanelProps } from '../../types'
import { useDispatch } from 'react-redux';
import { setActivePanel } from '../../store/slices/navigation';
import { SEARCH_PANEL } from '../../constants';

const MainPanel: React.FC<PanelProps> = ({
  id,
}) => {
  const dispatch = useDispatch()

  const goToSearchSchedule = () => {
    dispatch(setActivePanel(SEARCH_PANEL))
  }

  return (
    <Panel id={id} style={{paddingTop: 64}}>
      <Banner
        mode="image"
        header={true && 'Числитель'}
        subheader="До конца семестра"
        background={
          <div
            style={{
              backgroundColor: '#65c063',
              backgroundImage: 'url(https://sun9-59.userapi.com/7J6qHkTa_P8VKRTO5gkh6MizcCEefz04Y0gDmA/y6dSjdtPU4U.jpg)',
              backgroundPosition: 'right bottom',
              backgroundSize: 320,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={
          // <Button
          //   disabled
          //   mode="overlay_primary"
          // >Подробнее</Button>
          <InfoRow style={{marginTop:4}} header={`осталось ${5} дн.`}>
            <Progress
              style={{width:'70%', marginTop : 8, background : 'gray'}}
              value={(60 / 100) * 100}
            />
          </InfoRow>
        }
      />

      <Div>
        <Card>
          <Cell
            after={<Icon28Search/>}
            onClick={goToSearchSchedule}
          >Искать расписание</Cell>
        </Card>
      </Div>
      
      <Div>
        <Card>
          <Cell
            after={<Icon28SettingsOutline/>}
          >Настройки</Cell>
        </Card>
      </Div>
    </Panel>
  )
}

export default MainPanel