import { DayRange, Lesson, WeekRange } from "../types";

export const getLessonsInDay = (
  lessons: Lesson[],
  day: DayRange,
  week: WeekRange
): Lesson[] => {
  return lessons.filter(lesson => {
    const inDay = lesson.day === day
    const inWeek = lesson.week === week || lesson.week === 2

    return inDay && inWeek
  })
}